              //path_seq_data.StartCoordination = newStartCoordinates;
            }
          }
        }
      } catch (error) {
      }
      try {
        //
        var pathes = path_features.filter(feat => feat.get('end_pt_index') == pt_index)
        for (var i = 0; i < pathes.length; i++) {
          var path_feature = pathes[i]
