    ChangeToSelectAGVMode() {
      this.AGVLocLayer.setVisible(true);
      this.RestoredFillColorOfChangedFeature();
      //Feature
      this.ChangeFeaturesAsIgnoreStyle(this.StationPointsFeatures, 'rgb(222, 222, 222)');

