<template>
  <div style="height: 600px; width: 800px">
    <l-map :zoom="mapZoom" :center="mapCenter" style="height: 100%">
      <!-- <l-tile-layer :url="tileLayerUrl"></l-tile-layer> -->

      <!--  -->
      <l-geo-json :geojson="pointsGeoJson" :options="pointsGeoJsonOptions" />

      <!--  -->
      <l-geo-json :geojson="linesGeoJson" :options="linesGeoJsonOptions" />
    </l-map>
  </div>
</template>
  
  <script>
import { ref } from 'vue';
import "leaflet/dist/leaflet.css";
import { LMap, LTileLayer, LGeoJson } from "@vue-leaflet/vue-leaflet";

import { MapStore } from '@/components/Map/store'

export default {
  components: {
    LMap,
    LTileLayer,
    LGeoJson,
  },
  setup() {
    const mapCenter = ref([0, 0]); //  [0, 0]
    const mapZoom = ref(2); // 
    const tileLayerUrl = "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png";

    //  GeoJSON 
    var pointsGeoJson = ref({
      type: "FeatureCollection",
      features: [
        {
          type: "Feature",
          properties: {
            color: "red",
            name: "red658856885687"
          },
          geometry: {
            type: "Point",
            coordinates: [0, 1], // 
          },
        },
        {
          type: "Feature",
          properties: {
            color: "blue",
            name: "blue"
          },
          geometry: {
            type: "Point",
            coordinates: [0, 2], // 
          },
        },
        {
          type: "Feature",
          properties: {
            color: "green",
            name: "green"
          },
          geometry: {
            type: "Point",
            coordinates: [0, 3], // 
          },
        },
      ],
    });
    //  GeoJSON 
    var linesGeoJson = ref({
      type: "FeatureCollection",
      features: [
        {
          type: "Feature",
          geometry: {
            type: "LineString",
            coordinates: [
              [0, 1], // 
              [0, 2], // 
            ],
          },
        },
        {
          type: "Feature",
          geometry: {
            type: "LineString",
            coordinates: [
              [0, 1], // 
              [22, 3], // 
            ],
          },
        },
      ],
    });

    //  GeoJSON 
    const pointsGeoJsonOptions = ref({
      pointToLayer: (feature, latLng) => {
        const icon = L.divIcon({
          className: "custom-icon station-icon", //  CSS 
          html: `<span>${feature.properties.name}</span>`, // 
        });
        return L.marker(latLng, { icon });
      },
    });
    //  GeoJSON 
    const linesGeoJsonOptions = ref({
      style: {
        color: "green", // 
        weight: 3, // 
      },
    });

    async function GetMapData() {
      var geoMapdata = await MapStore.dispatch('DownloadGeoMapData')
      return geoMapdata;
    }
    return {
      mapCenter,
      mapZoom,
      tileLayerUrl,
      pointsGeoJson,
      pointsGeoJsonOptions,
      linesGeoJson,
      linesGeoJsonOptions,
      GetMapData
    };
  },
  async mounted() {
    var mapdata = await this.GetMapData()
    this.pointsGeoJson = mapdata.PointsGeoJsonData
    this.linesGeoJson = mapdata.LinesGeoJsonData
  }
};
  </script>
  
  <style lang="scss">
.custom-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #464646;
  border: 2px solid #333;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #333;
  font-weight: bold;

  span {
    position: absolute;
    color: blue;
    top: -20px;
    right: -40px;
    width: 50px;
    text-align: left;
  }
}

.station-icon {
  background-color: #ff0077;
}
</style>
  