<template>
  <div class="equipment-manager border-start border-end">
    <p class="text-start px-1">
      <b-button variant="primary" squared @click="SaveSettingHandler"></b-button>
      <b-button variant="info" squared class="mx-2" @click="AddNewEqHandler"></b-button>
      <b-button squared @click="ReloadSettingsHandler"></b-button>
    </p>
    <el-table
      :header-cell-style="{ color: 'white', backgroundColor: 'rgb(13, 110, 253)', fontSize: '16px' }"
      :data="EqDatas"
      size="small"
      border
      height="680"
      style="width:1800px">
      <el-table-column label="Index" prop="index" width="80" align="center" fixed="left" />
      <el-table-column label="" prop="Name" width="250" fixed="left">
        <template #default="scope">
          <div class="d-flex">
            <b-form-input
              :state="ValidName(scope.row)"
              v-model="scope.row.Name"
              placeholder=""
              style="width:120px"
              :no-wheel="true"
              size="sm"
              :min="1"
              @input="HandleEqNameChange(scope.row, scope.row.Name)"></b-form-input>
            <b-button
              class="mx-1"
              size="sm"
              variant="primary"
              @click="HandleUseMapDataDisplayName(scope.row.TagID)"></b-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="Tag ID" prop="TagID" width="100" align="center" fixed="left">
        <template #default="scope">
          <b-form-input
            type="number"
            :state="ValidTag(scope.row)"
            v-model.number="scope.row.TagID"
            placeholder="tag id"
            :no-wheel="true"
            size="sm"
            :min="1"></b-form-input>
        </template>
      </el-table-column>
      <el-table-column label="" prop="Region" width="130">
        <template #default="scope">
          <RegionsSelector v-model="scope.row.Region"></RegionsSelector>
        </template>
      </el-table-column>
      <el-table-column label="" width="550">
        <template #default="scope">
          <el-select
            v-model="scope.row.ValidDownStreamEndPointNames"
            multiple
            placeholder="Select"
            style="width: 500px">
            <el-option
              v-for="eq_name in GetAvaluableEqNameList(scope.row.Name)"
              :key="eq_name"
              :label="eq_name"
              :value="eq_name" />
          </el-select>
        </template>
      </el-table-column>
      <el-table-column label="" prop="ConnOptions.ConnMethod" width="140">
        <template #default="scope">
          <el-select v-model="scope.row.ConnOptions.ConnMethod" :size="cell_item_size">
            <el-option label="Modbus TCP" :value="0"></el-option>
            <el-option label="Modbus RTU" :value="1"></el-option>
            <el-option label="TCP/IP" :value="2"></el-option>
            <el-option label="Serial Port" :value="3"></el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column label="" width="120">
        <el-table-column label="IP" prop="ConnOptions.IP" width="150">
          <template #default="scope">
            <el-input
              :disabled="scope.row.ConnOptions.ConnMethod == 1"
              v-model="scope.row.ConnOptions.IP"
              :size="cell_item_size"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="Port" prop="ConnOptions.Port" width="120">
          <template #default="scope">
            <el-input
              :disabled="scope.row.ConnOptions.ConnMethod == 1"
              v-model.number="scope.row.ConnOptions.Port"
              :size="cell_item_size"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="ComPort" prop="ConnOptions.ComPort" width="120">
          <template #default="scope">
            <el-input
              :disabled="scope.row.ConnOptions.ConnMethod == 0"
              v-model="scope.row.ConnOptions.ComPort"
              :size="cell_item_size"></el-input>
          </template>
        </el-table-column>
      </el-table-column>
      <el-table-column label="" min-width="270" fixed="right">
        <template #default="scope">
          <div>
            <el-button :size="cell_item_size" type="danger" @click="RemoveHandle(scope.row)"></el-button>
            <el-button
              :size="cell_item_size"
              type="default"
              @click="ConnectTestHandle(scope.row)"></el-button>
            <el-button
              :size="cell_item_size"
              type="primary"
              @click="IOCheckBtnHandle(scope.row)">IO</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-drawer v-model="io_check_drawer" direction="btt">
      <div class="hs-signals d-flex">
        <div class="mx-3">-EQ</div>
        <div
          class="di-status"
          @click="HandleHSsignaleChange(selected_eq_io_data.EQName, 'L_REQ', !scope.row.HS_EQ_L_REQ)"
          v-bind:style="signalOn(selected_eq_io_data.HS_EQ_L_REQ)">L_REQ</div>
        <div
          class="di-status"
          @click="HandleHSsignaleChange(selected_eq_io_data.EQName, 'U_REQ', !scope.row.HS_EQ_U_REQ)"
          v-bind:style="signalOn(selected_eq_io_data.HS_EQ_U_REQ)">U_REQ</div>
        <div
          class="di-status"
          @click="HandleHSsignaleChange(selected_eq_io_data.EQName, 'READY', !scope.row.HS_EQ_READY)"
          v-bind:style="signalOn(selected_eq_io_data.HS_EQ_READY)">READY</div>
        <div
          class="di-status"
          @click="HandleHSsignaleChange(selected_eq_io_data.EQName, 'UP_READY', !scope.row.HS_EQ_UP_READY)"
          v-bind:style="signalOn(selected_eq_io_data.HS_EQ_UP_READY)">UP_READY</div>
        <div
          class="di-status"
          @click="HandleHSsignaleChange(selected_eq_io_data.EQName, 'LOW_READY', !scope.row.HS_EQ_LOW_READY)"
          v-bind:style="signalOn(selected_eq_io_data.HS_EQ_LOW_READY)">LOW_READY</div>
        <div
          v-if="false"
          class="di-status"
          @click="HandleHSsignaleChange(selected_eq_io_data.EQName, 'BUSY', !scope.row.HS_EQ_BUSY)"
          v-bind:style="signalOn(selected_eq_io_data.HS_EQ_BUSY)">BUSY</div>
      </div>
      <div class="hs-signals d-flex">
        <div class="mx-3">-AGV</div>
        <div
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'To_EQ_Up', !scope.row.To_EQ_Up)"
          v-bind:style="signalOn(selected_eq_io_data.To_EQ_Up)">To_EQ_Up</div>
        <div
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'To_EQ_Low', !scope.row.To_EQ_Low)"
          v-bind:style="signalOn(selected_eq_io_data.To_EQ_Low)">To_EQ_Low</div>
        <div
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'VALID', !scope.row.HS_AGV_VALID)"
          v-bind:style="signalOn(selected_eq_io_data.HS_AGV_VALID)">VALID</div>
        <div
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'TR_REQ', !scope.row.HS_AGV_TR_REQ)"
          v-bind:style="signalOn(selected_eq_io_data.HS_AGV_TR_REQ)">TR_REQ</div>
        <div
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'BUSY', !scope.row.HS_AGV_BUSY)"
          v-bind:style="signalOn(selected_eq_io_data.HS_AGV_BUSY)">BUSY</div>
        <div
          v-if="false"
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'READY', !scope.row.HS_AGV_READY)"
          v-bind:style="signalOn(selected_eq_io_data.HS_AGV_READY)">READY</div>
        <div
          class="di-status"
          @click="HandleAGVHSSignaleChange(selected_eq_io_data.EQName, 'COMPT', !scope.row.HS_AGV_COMPT)"
          v-bind:style="signalOn(selected_eq_io_data.HS_AGV_COMPT)">COMPT</div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { GetEQOptions, SaveEQOptions, ConnectTest } from '@/api/EquipmentAPI.js';
import RegionsSelector from '@/components/RegionsSelector.vue'
import { MapStore } from '../Map/store';
import { EqStore } from '@/store'
import { ElNotification } from 'element-plus';
import { duration } from 'moment';
export default {
  components: {
    RegionsSelector,
  },
  data() {
    return {
      cell_item_size: '',
      io_check_drawer: false,
      EqDatas: [
        // {
        //   Name: "123",
        //   TagID: 1,
        //   ValidDownStreamEndPointNames: [],
        //   ConnOptions: {
        //     ConnMethod: 0,
        //     IP: "10.0.0.1",
        //     Port: 502,
        //     ComPort: "COM1"
        //   }
        // }
      ],
      EqDatas_Orignal: [],
      ValidTag: (row_) => {
        var tag = row_.TagID;
        var others_row = this.EqDatas.filter(d => d.Name != row_.Name)
        var same_tag_rows = others_row.filter(row => row.TagID == tag)
        return same_tag_rows.length == 0 && tag >= 1;
      },
      ValidName: (row_) => {
        var name = row_.Name;
        var same_name_row = this.EqDatas.filter(d => d.Name == row_.Name)
        return same_name_row.length == 1 && name != '';
      },
      selected_eq: {}

    }
  },
  methods: {
    GetAvaluableEqNameList(expect_name) {
      return this.EqNames.filter(name => name != expect_name);
    },
    async SaveSettingHandler() {
      var ret = await SaveEQOptions(this.EqDatas);
      if (ret.confirm) {
        this.$swal.fire({
          title: '',
          icon: 'success',
          timer: 2000
        })
      }
      else {
        this.$swal.fire({
          title: '',
          text: ret.message,
          icon: 'error',
        })

      }
    },
    async DownloadEQOptions() {
      this.EqDatas = [];

      var datas = await GetEQOptions()
      for (let index = 0; index < datas.length; index++) {
        const element = datas[index];
        element.index = index;
        this.EqDatas.push(element)
      }
      this.CloneEQDatas();
    },
    CloneEQDatas() {
      this.EqDatas_Orignal = JSON.parse(JSON.stringify(this.EqDatas));
    },
    ReloadSettingsHandler() {
      this.DownloadEQOptions();
    },
    AddNewEqHandler() {
      this.EqDatas.push(
        {
          index: this.EqDatas.length,
          Name: `New_EQ_${this.EqDatas.length}`,
          TagID: 1,
          ConnOptions: {
            ConnMethod: 0,
            IP: "127.0.0.1",
            Port: 502,
            ComPort: "COM1"
          }
        })
    },
    RemoveHandle(row) {
      var remains = this.EqDatas.filter(eq => eq.Name != row.Name)
      this.EqDatas = remains;
    },
    async IOCheckBtnHandle(row) {
      this.selected_eq = row;
      this.io_check_drawer = true;
    },
    async ConnectTestHandle(row) {
      var ret = await ConnectTest(row.ConnOptions)
      var display_text = ''
      if (row.ConnOptions.ConnMethod == 0) {
        display_text = `Modbus TCP - ${row.ConnOptions.IP}:${row.ConnOptions.Port}`
      } else {
        display_text = `Modbus RTU - ${row.ConnOptions.ComPort}`
      }

      if (ret.Connected) {
        this.$swal.fire({
          title: 'OK',
          text: display_text,
          icon: 'success'

        })
      } else {
        this.$swal.fire({
          title: 'Fail',
          text: display_text,
          icon: 'error'

        })
      }
    },
    beforeRouteLeave(to, from, next) {
      alert('leave!')
    },
    async HandleUseMapDataDisplayName(tag) {
      var mapPoint = await MapStore.dispatch('GetMapPointByTag', tag)
      if (mapPoint) {
        var row = this.EqDatas.find(eq => eq.TagID == tag);
        var _name = mapPoint.Graph.Display;
        row.Name = _name;
        this.HandleEqNameChange(row, mapPoint.Name);
        ElNotification({
          message: `Get Display Name From Map Success(Tag ${tag} = ${_name})`,
          duration: 1000,
          type: 'success',
          title: ''
        })
      } else {
        ElNotification({
          message: `Get Display Name From Map Fail`,
          duration: 1000,
          type: 'error',
          title: ''
        })
      }

    },
    HandleEqNameChange(row, newName) {
      var tagid = row.TagID;
      var oriOptions = this.EqDatas_Orignal.find(d => d.TagID == tagid);
      var oriName = oriOptions.Name;
      var useOriNameOptionsList = this.EqDatas.filter(eq => eq.ValidDownStreamEndPointNames.includes(oriName));
      useOriNameOptionsList.forEach(option => {
        var _index = option.ValidDownStreamEndPointNames.indexOf(oriName);
        option.ValidDownStreamEndPointNames[_index] = newName;
      });
      this.CloneEQDatas();
    }
  },
  mounted() {

    this.DownloadEQOptions();
  },
  computed: {
    EqNames() {
      return this.EqDatas.map(ep => ep.Name);
    },
    eq_data() {
      return EqStore.getters.EQData
    },
    selected_eq_io_data() {
      return this.eq_data.find(eq => eq.EQName == this.selected_eq.EQName)
    }
  },
}
</script>

<style lang="scss" scoped>
.equipment-manager {}
</style>